export const setIsPageLoading = (state, isPageLoading) => {
	state.isPageLoading = isPageLoading;
}
export const setUserAccessToken = (state, userAccessToken) => {
	state.userAccessToken = userAccessToken;
}
export const setUserId = (state, userId) => {
	state.userId = userId;
}
export const setUserAvatar = (state, userAvatar) => {
	state.userAvatar = userAvatar;
}
export const setIsUserActive = (state, isUserActive) => {
	state.isUserActive = isUserActive;
}
export const setProfiles = (state, profiles) => {
	state.profiles = profiles;
}



