<template>
    <div id="bloodBank">
      <p class="pageTitle">{{ title }}</p>
      <p class="appAlert" v-if="isFinished" v-bind:class="{ alertError: isError }">{{ alertmsg }}</p>
      <div class="row">
        <a v-for="occupation in occupations" :href="'#/profiles/occupation/' + occupation.id" class="bloodBankItem col-4">
          <div class="occupationItemIcon itemIcon">
            <i class="material-icons">work</i>
          </div>
          <p class="textCenter occupationText">{{ occupation.title }}</p>
        </a>
      </div>
    </div>
</template>

<script>

import Vue from 'vue'
import { mapGetters } from 'vuex'

export default {

  name: 'Occupations',
  data () {
    return {
    	apiUrl: Vue.config.apiUrl,
      title: 'Occupations',
      occupations: [],
      alertmsg: '',
      isFinished: false,
      isError: false,
    }
  },
  methods: {
    getOccupationsList: function() {
      this.$store.commit('setIsPageLoading', true);
      this.$http.post(this.apiUrl + 'getOccupationsList', {}, {
        headers: {
          'userToken': this.userAccessToken,
        }
      }).then(response => {
            this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            if(body.status == 'success') {
              this.occupations = body.occupations;

            } else if(body.status == 'error') {
              this.isError = true;
              this.alertmsg = 'Something went wrong, please try again';
            }
        }).catch(function(err) {
          this.$store.commit('setIsPageLoading', false);
          this.isError = true;
          this.alertmsg = 'Something went wrong, please try again';
          console.log('error', err);
        })
    }
  },
  computed: {
    ...mapGetters(['userAccessToken'])
  },
  created: function() {
    this.getOccupationsList();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



