<template>
  <div id="choosePassword">
    <p>Please choose a password so that you can login with the email and password as well.</p>
    <form class="form" v-on:submit.prevent="savePassword">
      <p class="appAlert" v-if="isFinished" v-bind:class="{ alertSuccess: isSuccess, alertError: isError }">{{ alertmsg }}</p>
      <div class="form-group">
        <label>Password</label>
        <div class="innerFormGroup">
          <input type="password" v-model="password" class="appInnerInput" :class="{ errorText: errors.password }" placeholder="Password">
        </div>
      </div>
      <div class="form-group">
        <label>Confirm Password</label>
        <div class="innerFormGroup">
          <input type="password" v-model="confirmPassword" class="appInnerInput" :class="{ errorText: errors.confirmPassword }" placeholder="Confirm Password">
        </div>
      </div>
      <div class="form-group">
        <button class="appPrimaryBtn btnFulWidth marginTop15">Save</button>
      </div>
    </form>
  </div>
</template>

<script>

import Vue from 'vue'
import { mapGetters } from 'vuex'

export default {
  
  name: 'ChoosePassword',
  data () {
    return {
      password: '',
      confirmPassword: '',
      errors: {
        password: false,
        confirmPassword: false
      },
      error: false,
      isFinished: false,
      isSuccess: false,
      isError: false,
      alertmsg: '',
    	apiUrl: Vue.config.apiUrl,
    }
  },
  methods: {
    savePassword: function() {
      this.error = false;
      if(!this.password)
        this.error = this.errors.password = true;
      else
        this.errors.password = false;

      if(!this.confirmPassword)
        this.error = this.errors.confirmPassword = true;
      else if(this.password != this.confirmPassword)
        this.error = this.errors.confirmPassword = true;
      else
        this.errors.confirmPassword = false;

      if(this.error) return false;

      this.$store.commit('setIsPageLoading', true);
      this.$http.post(this.apiUrl + 'savePassword', { password: this.password, userId: this.userId }, {
        headers: {
          'userToken': this.userAccessToken
        }
      }).then(response => {
          this.$store.commit('setIsPageLoading', false);
          var body = response.body;
          if(body.status == 'success') {
              // this.alertmsg = 'Your password has been set';
              this.isSuccess = this.isFinished = true;
              alert('Your password has been set');
              window.location.href = '#/dashboard';

          } else if(body.status == 'error') {
              this.isError = this.isFinished = true;
              this.alertmsg = body.message;
          }
      }).catch(function(error){
          this.$store.commit('setIsPageLoading', false);
          this.isError = this.isFinished = true;
          this.alertmsg = 'Something went wrong, please try again';
          console.log('error', error);
        })
    }
  },
  computed: {
    ...mapGetters(['userAccessToken', 'userId'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



