<template>
  <div id="dashboard">
    <p class="pageTitle">Welcome to the {{ title }}..!!!</p>
    <div class="wrapper container-fluid">
      <div class="row">
        <a class="homeItem col-4" href="#/occupations">
          <div class="itemIcon">
            <i class="material-icons">work</i>
          </div>
          <p class="textCenter">Occupations</p>
        </a>
        <a class="homeItem col-4" href="#/bloodBank">
          <div class="itemIcon">
            <i class="material-icons">local_hospital</i>
          </div>
          <p class="textCenter">Blood Bank</p>
        </a>
        <a class="homeItem col-4" href="#/searchProfiles">
          <div class="itemIcon">
            <i class="material-icons">search</i>
          </div>
          <p class="textCenter">Search</p>
        </a>
        <a class="homeItem col-4" :href="'#/profile/' + userId">
          <div class="itemIcon">
            <i class="material-icons">person</i>
          </div>
          <p class="textCenter">My Profile</p>
        </a>
        <a class="homeItem col-4" href="#/about/Me">
          <div class="itemIcon">
            <i class="material-icons">info</i>
          </div>
          <p class="textCenter">About</p>
        </a>

        <a class="homeItem col-4" href="#/about/TYSAS">
          <div class="itemIcon">
            <i class="material-icons">title</i>
          </div>
          <p class="textCenter">About TYSAS</p>
        </a>
        <a class="homeItem col-4" href="#/notAvailable">
          <div class="itemIcon">
            <i class="material-icons">settings</i>
          </div>
          <p class="textCenter">Settings</p>
        </a>
      </div>
    </div>
  </div>
</template>

<script>

import Vue from 'vue'
import { mapGetters } from 'vuex'

export default {
  name: 'Dashboard',
  data () {
    return {
      apiUrl: Vue.config.apiUrl,
    	storagePath: Vue.config.storagePath,
      title: 'Dashboard',
    }
  },
  methods: {
    getUserAvatar: function() {
      this.$http.post(this.apiUrl + 'getUserAvatar', {}, {
        headers: {
          'userToken': this.userAccessToken,
        }
      }).then(response => {
            var body = response.body;
            if(body.status == 'success' && body.avatar) {
              this.$store.commit('setUserAvatar', this.storagePath + body.avatar);
            }
        }).catch(function(err) {

        })
    }
  },
  computed: {
    ...mapGetters(['userAccessToken', 'isUserActive', 'userId'])
  },
  created() {
    this.getUserAvatar();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



