// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import vueResource from 'vue-resource'
import * as VueGoogleMaps from 'vue2-google-maps'
// import facebookLogin from 'facebook-login-vuejs';

// Components
import Header from './components/Header'
import Footer from './components/Footer'
import Menu from './components/Menu'
import More from './components/More'
import Loader from './components/Loader'

Vue.component('app-header', Header);
Vue.component('app-footer', Footer);
Vue.component('app-menu', Menu);
Vue.component('more-menu', More);
Vue.component('app-loader', Loader);

Vue.use(vueResource);
Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyBsid1gptxPvc3ojefUbvBpe7eo8xcXdBI',
    libraries: 'places', // This is required if you use the Autocomplete plugin
    // OR: libraries: 'places,drawing'
    // OR: libraries: 'places,drawing,visualization'
    // (as you require)
  }
});

Vue.config.productionTip = false
Vue.http.options.emulateJSON = true
Vue.config.appName = 'MyHomeTown'
Vue.config.appVersion = '1.0.0'
Vue.config.apiUrl = 'http://mhtapi.rafeeque.in/api/'
// Vue.config.apiUrl = 'http://local.myhometown.in/api/'
Vue.config.storagePath = 'http://mhtapi.rafeeque.in/storage/'
// Vue.config.storagePath = 'http://local.myhometown.in/storage/'
Vue.config.facebookAppID = '1703957103221700'
Vue.config.googleClientID = '875678259760-sjnemnrfo5mf31hcovemjp0ctp8mk46f'
/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>'
})



