<template>
  <div id="login">
    <div class="wrapper">
      <h3 class="pageTitle">{{ title }}</h3><br>
		<p class="loginWithText textCenter">--&nbsp; Login with &nbsp;--</p>
    <facebook-login class="button btnFulWidth FBLoginBtn" :appId="facebook.appID" :loginLabel="'Facebook Account'" v-on:login="facebookLogin" :loginOptions="facebook.loginOptions"></facebook-login>
    <div class="googleLogin">
      <span class="googleLoginIcon"></span>
      <g-signin-button :params="googleSignInParams" v-on:success="googleLoginSuccess" v-on:error="googleLoginError">Google Account</g-signin-button>
    </div>
    <!-- <ul class="socialIcons">
			<li class="iconFB"></li>
			<li class="iconGL"></li>
		</ul> -->
		<p class="textCenter">or</p>
		<form class="appForm" v-on:submit.prevent="loginSubmit">
			<p class="textCenter formError errorText" v-if="login.errorText">{{ login.errorText }}</p>
			<div class="formGroup">
				<input type="text" v-model="login.username" :class="{ errorText: login.isUsernameError }" value="" class="appInput loginInput" placeholder="Email or Mobile Number">
			</div>
			<div class="formGroup">
				<input type="password" v-model="login.password" :class="{ errorText: login.isPasswordError }" class="appInput loginInput" value="" placeholder="Password">
			</div>
			<div class="formGroup">
				<button type="submit" class="appPrimaryBtn btnFulWidth">Login</button>
			</div>
		</form>
		<ul class="appBottomLinks">
			<li><a href="#/forgot">Forgot password?</a></li>
			<li><a href="#/signup">Create new account</a></li>
		</ul>
	</div>
  </div>
</template>

<script>

import '../assets/js/googleApi.js'
import facebookLogin from 'facebook-login-vuejs'
import GSignInButton from 'vue-google-signin-button'
import Vue from 'vue'

Vue.use(GSignInButton)

export default {
  name: 'Login',
  data () {
    return {
      title: 'Login',
    	login: {
    		username: '',
    		password: '',
    		isError: false,
    		errorText: '',
    		isUsernameError: false,
    		isPasswordError: false
    	},
      apiUrl: Vue.config.apiUrl,
      facebook: {
        loginOptions: {
          scope: 'email'
        },
    	  appID: Vue.config.facebookAppID,
      },
      googleSignInParams: {
        client_id: Vue.config.googleClientID + '.apps.googleusercontent.com',
        scope: 'email profile',
        cookiepolicy: 'single_host_origin',
      }
    }
  },
  methods: {

  	loginSubmit: function() {

      this.isError = false;
      this.login.errorText = '';
  		if(!this.login.username || !this.validateEmail(this.login.username)) {
  			this.isError = this.login.isUsernameError = true;
  		} else {
  			this.login.isUsernameError = false;
  		}
  		if(!this.login.password) {
  			this.isError = this.login.isPasswordError = true;
  		} else {
  			this.login.isPasswordError = false;
  		}

  		if(this.isError) return false;
      alert(this.apiUrl + 'login');
      this.$store.commit('setIsPageLoading', true);
  		this.$http.post(this.apiUrl + 'login',
            {
                username: this.login.username,
                password: this.login.password
            }
        ).then(response => {
            this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            if(body.status == 'success') {
                this.$store.commit('setUserAccessToken', body.token);
                this.$store.commit('setUserId', body.userId);
                this.$store.commit('setIsUserActive', true);
                window.location.href = '#/dashboard';

            } else if(body.status == 'error') {
                this.login.isError = true;
                this.login.errorText = body.message;
                this.login.password = '';
            }
        }).catch(function(error){
          alert(error);
          this.$store.commit('setIsPageLoading', false);
          // alert(error.parseJson());
          this.login.isError = true;
          this.login.errorText = error.bodyText;
          alert(error.statusText);
          // console.log('error', error);
        })
  	},
    facebookLogin: function(response) {
      var response = response.response;
      if(response.status == 'connected') {
        FB.api('/me', { fields: 'email,name' }, user => {
          var userData = {
              fullName: user.name,
              username: user.email,
              accessToken: response.authResponse.accessToken,
              source: 'facebook'
          };
          this.socialLogin(userData);
        })
      }
    },
    googleLoginSuccess: function(googleUser) {
      var profile = googleUser.getBasicProfile() // etc etc
      var userData = {
        fullName: profile.getName(),
        username: profile.getEmail(),
        accessToken: googleUser.Zi.access_token,
        source: 'google'
      };
      this.socialLogin(userData);
    },
    googleLoginError (error) {
      console.log('error', error)
    },
    socialLogin: function(data) {
      this.$store.commit('setIsPageLoading', true);
      this.$http.post(this.apiUrl + 'socialLogin', data).then(response => {
        this.$store.commit('setIsPageLoading', false);
        var body = response.body;
        if(body.status == 'success') {
            this.$store.commit('setUserAccessToken', body.token);
            this.$store.commit('setUserId', body.userId);
            this.$store.commit('setIsUserActive', true);
            window.location.href = body.userHasPassword ? '#/dashboard' : '#/choosePassword';

        } else if(body.status == 'error') {
            this.login.isError = true;
            this.login.errorText = body.message;
            this.login.password = '';
        }
      }).catch(function(error){
        this.$store.commit('setIsPageLoading', false);
        console.log('error', error);
      })
    },
  	validateEmail: function(email) {
  		var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
  	}
  },
  components: {
    facebookLogin
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



