<template>
    <!-- Menu Start -->
    <div id="menu">
      <div id="menuWrapper">
        <div class="menuUserArea" v-on:click="menuProfileClick">
          <!-- <i class="material-icons menuUserIcon">person_outline</i> -->
          <center><img :src="avatarSrc" class="menuUserIcon"></center>
          <span class="menuUsername">Rafeeque</span>
        </div>
        <ul class="menuItems">
          <li v-on:click="menuClick"><a href="#/dashboard"><i class="material-icons">home</i><span class="menuItemText">Home</span></a></li>
          <li v-on:click="menuClick"><a href="#/occupations"><i class="material-icons">work</i><span class="menuItemText">Occupations</span></a></li>
          <li v-on:click="menuClick"><a href="#/bloodBank"><i class="material-icons">local_hospital</i><span class="menuItemText">Blood Bank</span></a></li>
          <li v-on:click="menuClick"><a href="#/searchProfiles"><i class="material-icons">search</i><span class="menuItemText">Search</span></a></li>
          <li v-on:click="menuClick"><a href="#/about/Me"><i class="material-icons">info</i><span class="menuItemText">About</span></a></li>
        </ul>
        <p class="menuAppInfo">{{ appName }} {{ appVersion }}</p>
      </div>
    </div>
    <!-- Menu End -->
</template>

<script>

import Vue from 'vue'
import { mapGetters } from 'vuex'

export default {
  name: 'Menu',
  data () {
    return {
    	apiUrl: Vue.config.apiUrl,
      appName: Vue.config.appName,
      appVersion: Vue.config.appVersion,
      storagePath: Vue.config.storagePath,
      userName: 'User',
      avatarSrc: require('../assets/images/avatar.png'),
    }
  },
  methods: {
    menuClick: function() {
      this.$root.$emit('menuClickEvent', false);
    },
    menuProfileClick: function() {
      this.$root.$emit('menuClickEvent', false);
      window.location.href = '#/profile/' + this.userId;
    }
  },
  computed: {
    ...mapGetters(['userId', 'userAccessToken', 'userAvatar'])
  },
  created: function() {
    this.avatarSrc = this.userAvatar;
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



