<template>
    <!-- Menu Start -->
    <div id="moreMenu">
      <span class="upArrow"></span>
      <!-- <div class="moreMenuUserArea">
        <i class="material-icons moreMenuUserIcon">person_outline</i>
        <span class="moreMenuUsername">Rafeeque</span>
      </div> -->
      <ul class="moreMenuItems">
        <li><a v-on:click="moreMenuClick" href="#/editProfile"><i class="material-icons">person_outline</i><span class="moreMenuItemText">Edit Profile</span></a></li>
        <li><a href="#" v-on:click.prevent="logoutUser"><i class="material-icons">power_settings_new</i><span class="moreMenuItemText">Logout</span></a></li>
      </ul>
    </div>
    <!-- Menu End -->
</template>

<script>

import Vue from 'vue'
import { mapGetters } from 'vuex'

export default {
  // props: ['showMore'],
  name: 'More',
  data () {
    return {
    	apiUrl: Vue.config.apiUrl,
      // showMoreMe: this.showMore
    }
  },
  methods: {
    logoutUser: function() {
      this.$store.commit('setIsPageLoading', true);
      this.moreMenuClick();
      this.$http.post(this.apiUrl + 'logout', {}, {
        headers: {
          'userToken': this.userAccessToken
        }
      }
        ).then(response => {
          this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            if(body.status == 'success') {
                this.$store.commit('setUserAccessToken', '');
                this.$store.commit('setIsUserActive', false);
                window.location.href = '#/';

            } else if(body.status == 'error') {
                alert(body.message); return false;
            }
        }).catch(function(err){
          this.$store.commit('setIsPageLoading', false);
          console.log('error', err);
          alert('Something went wrong, please try again'); return false;
        })
    },
    moreMenuClick: function() {
      this.$root.$emit('moreMenuEvent', false);
    }
  },
  computed: {
    ...mapGetters(['userAccessToken'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



