<template>
    <div id="bloodBank">
      <p class="pageTitle">{{ title }}</p>
      <form class="form" v-on:submit.prevent="searchProfiles">
        <div class="form-group">
          <label>Enter the name you want to search</label>
          <input type="text" v-model="search" class="appInnerInput profilesSearchInput" placeholder="Search Profiles Here">
        </div>
        <div class="form-group">
          <button class="appPrimaryBtn btnFulWidth marginTop5">Search</button>
        </div>
      </form>
    </div>
</template>

<script>

import Vue from 'vue'

export default {

  name: 'SearchProfiles',
  data () {
    return {
    	apiUrl: Vue.config.apiUrl,
      title: 'Search Profiles',
      search: '',
    }
  },
  methods: {
    searchProfiles: function() {
      window.location.href = '#/profiles/search/' + this.search
    }
  },
  computed: {

  },
  created: function() {

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



