<template>
  <div id="app">
    <app-header></app-header>
    <div id="appContent">
      <router-view/>
    </div>
    <app-loader v-if="isPageLoading"></app-loader>
    <app-footer></app-footer>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'

export default {
  name: 'App',
  created() {},
  computed: {
    ...mapGetters(['isUserActive', 'isPageLoading'])
  }
}
</script>

<style>

