<template>
    <div id="profiles">
      <!-- <p class="pageTitle">{{ title }}</p> -->
      <!-- <span class="filterBy">Filter By,</span> -->
      <!-- <ul class="profileFilterByLocation">
        <li :class="{ disabledBtn: !myLocation }" v-on:click="locationChangeMy"><i class="material-icons">{{ myLocation ? 'location_on' : 'location_off' }}</i><span>My Location</span></li>
        <li :class="{ disabledBtn: myLocation }" v-on:click="locationChangeCurrent"><i class="material-icons">{{ myLocation ? 'location_off' : 'location_on' }}</i><span>Current Location</span></li>
      </ul> -->
      <input type="text" v-if="profiles.length" v-model="search" class="appInnerInput profilesSearchInput" placeholder="Search Profiles Here">
      <p class="textCenter marginTop15" v-if="!filteredProfiles.length && !alertmsg">No profiles found</p>
      <p class="appAlert" v-if="isFinished" v-bind:class="{ alertError: isError }">{{ alertmsg }}</p>
      <template v-if="filteredProfiles.length">
        <div class="row">
          <a v-for="profile in filteredProfiles" :href="'#/profile/' + profile.id" class="profilesItem col-4">
            <div class="profilesItem">
              <img :src="(profile.avatarPath ? storagePath + profile.avatarPath : avatarSrc)">
            </div>
            <p class="textCenter">{{ profile.fullName }}</p>
            <p class="textCenter profileMobileNo">{{ profile.mobileNo ? profile.mobileNo : 'Not Available' }}</p>
          </a>
        </div>
      </template>
    </div>
</template>

<script>

import Vue from 'vue'
import { mapGetters } from 'vuex'

export default {

  props: ['section', 'id'],
  name: 'Profiles',
  data () {
    return {
      apiUrl: Vue.config.apiUrl,
    	storagePath: Vue.config.storagePath,
      avatarSrc: require('../assets/images/avatar.png'),
      title: 'Profiles',
      isError: false,
      isFinished: false,
      alertmsg: '',
      profiles: [],
      currentLocation: {
        lat: 0,
        lng: 0,
      },
      myLocation: true,
      search: ''
    }
  },
  methods: {
    getProfiles: function() {
      this.$store.commit('setIsPageLoading', true);
      var data = {
        section: this.section,
        id: this.id,
        isMyLocation: this.myLocation ? 1 : 0
      };
      if(!this.myLocation) {
        data.currentLat = this.currentLocation.lat
        data.currentLng = this.currentLocation.lng
      }
      this.$http.post(this.apiUrl + 'getProfiles', data, {
        headers: {
          'userToken': this.userAccessToken,
        }
      }).then(response => {
            this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            this.isFinished = true;
            if(body.status == 'success') {
              this.$store.commit('setProfiles', body.profiles);
              this.profiles = body.profiles;
              this.alertmsg = '';
            } else if(body.status == 'error') {
              this.$store.commit('setProfiles', []);
              this.profiles = [];
              this.isError = true;
              this.alertmsg = body.message;
            }
        }).catch(function(err) {
          this.$store.commit('setIsPageLoading', false);
          this.$store.commit('setProfiles', []);
          this.profiles = [];
          this.isFinished = this.isError = true;
          this.alertmsg = 'Something went wrong, please try again';
          console.log('error', err);
        })
    },
    locationChangeMy: function() {
      if(!this.myLocation) {
        this.myLocation = !this.myLocation;
        this.getProfiles();
      }
    },
    locationChangeCurrent: function() {
      if(this.myLocation) {
        this.myLocation = !this.myLocation;
        var self = this;
        this.setCurrentLocation(function() {
          self.getProfiles();
        })
      }
    },
    setCurrentLocation: function(callback) {
      this.$store.commit('setIsPageLoading', true);
      var self = this;
      navigator.geolocation.getCurrentPosition((position) => {
        self.currentLocation = {
          lat: position.coords.latitude,
          lng: position.coords.longitude
        }
        self.$store.commit('setIsPageLoading', false);
        callback();
      })
    }
  },
  computed: {
    ...mapGetters(['userAccessToken']),

    filteredProfiles: function() {
      return this.$store.state.profiles.filter(item =>
        (item.fullName.toLowerCase().indexOf(this.search.toLowerCase()) > -1));
    }
  },
  created: function() {
    this.profiles = [];
    this.getProfiles();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



