<template>
  <div id="profile">
    <p class="pageTitle">{{ title }}</p>
    <div class="profileForm">
      <!-- Primary details section -->
      <div class="section">
        <div class="sectionHeader">
          <span class="sectionHeaderText">Primary Details</span>
          <i class="material-icons profileExpandIcon" v-if="!isPrimaryExpanded" v-on:click="isPrimaryExpanded = !isPrimaryExpanded">add</i>
          <i class="material-icons profileCollapseIcon" v-if="isPrimaryExpanded" v-on:click="isPrimaryExpanded = !isPrimaryExpanded">remove</i>
        </div>
        <transition name="fade">
          <div v-if="isPrimaryExpanded" class="sectionContent">
            <form class="form" v-on:submit.prevent="savePrimaryDetails" enctype="multipart/form-data">
              <p class="appAlert" v-if="user.primary.isPrimaryFinished" v-bind:class="{ alertSuccess: user.primary.isPrimarySuccess, alertError: user.primary.isPrimaryError }">{{ user.primary.primaryAlertmsg }}</p>
              <div class="profileAvatarDiv">
                <span class="errorText" v-if="user.primary.errors.avatar">Please Choose an Avatar</span>
                <img v-bind:src="user.primary.avatarSrc" width="100">
                <input type="file" class="fileUploadBtn" v-on:change="avatarChoosen">
              </div>
              <div class="form-group">
                <label>Full Name</label>
                <div class="innerFormGroup">
                  <input type="text" v-model="user.primary.name" class="appInnerInput" :class="{ errorText: user.primary.errors.name }" placeholder="Full Name">
                </div>
              </div>
              <div class="form-group">
                <label>Gender</label>
                <div class="innerFormGroup">
                  <select v-model="user.primary.gender" class="appInnerInput" :class="{ errorText: user.primary.errors.gender }">
                    <option value="">-- select --</option>
                    <option value="Male">Male</option>
                    <option value="Female">Female</option>
                  </select>
                </div>
              </div>
              <div class="form-group">
                <label>Date of Birth</label>
                <div class="innerFormGroup">
                  <datepicker :format="'dd-MM-yyyy'" v-model="user.primary.dob" :placeholder="'Date of Birth'" :required="true" :class="{ errorText: user.primary.errors.dob }"></datepicker>
                </div>
              </div>
              <div class="form-group">
                <label>Mobile Number</label>
                <div class="innerFormGroup">
                  <input type="text" v-model="user.primary.mobile" class="appInnerInput" placeholder="Mobile Number" :class="{ errorText: user.primary.errors.mobile }">
                </div>
              </div>
              <div class="form-group">
                <button class="appPrimaryBtn btnFulWidth marginTop15">Save</button>
              </div>
            </form>
          </div>
        </transition>
      </div>
      <!-- Password -->
      <div class="section">
        <div class="sectionHeader">
          <span class="sectionHeaderText">Password</span>
          <i class="material-icons profileExpandIcon" v-if="!isPasswordExpanded" v-on:click="isPasswordExpanded = !isPasswordExpanded">add</i>
          <i class="material-icons profileCollapseIcon" v-if="isPasswordExpanded" v-on:click="isPasswordExpanded = !isPasswordExpanded">remove</i>
        </div>
        <transition name="fade">
          <div v-if="isPasswordExpanded" class="sectionContent">
            <form class="form" v-on:submit.prevent="updatePassword">
              <p class="appAlert" v-if="user.password.isPasswordFinished" v-bind:class="{ alertSuccess: user.password.isPasswordSuccess, alertError: user.password.isPasswordError }">{{ user.password.passwordAlertmsg }}</p>
              <div class="form-group">
                <label>Current Password</label>
                <div class="innerFormGroup">
                  <input type="password" v-model="user.password.currPassword" class="appInnerInput" :class="{ errorText: user.password.errors.currPassword }" placeholder="Current Password">
