              </div>
              <div class="form-group">
                <label>New Password</label>
                <div class="innerFormGroup">
                  <input type="password" v-model="user.password.newPassword" class="appInnerInput" :class="{ errorText: user.password.errors.newPassword }" placeholder="New Password">
