              </div>
              <div class="form-group">
                <label>Confirm New Password</label>
                <div class="innerFormGroup">
                  <input type="password" v-model="user.password.confirmPassword" class="appInnerInput" :class="{ errorText: user.password.errors.confirmPassword }" placeholder="Confirm New Password">
                </div>
              </div>
              <div class="form-group">
                <button class="appPrimaryBtn btnFulWidth marginTop15">Save</button>
              </div>
            </form>
          </div>
        </transition>
      </div>
      <!-- Home address -->
      <div class="section">
