          <span class="sectionHeaderText">Home Address</span>
          <i class="material-icons profileExpandIcon" v-if="!isHomeAddrExpanded" v-on:click="isHomeAddrExpanded = !isHomeAddrExpanded">add</i>
          <i class="material-icons profileCollapseIcon" v-if="isHomeAddrExpanded" v-on:click="isHomeAddrExpanded = !isHomeAddrExpanded">remove</i>
        </div>
        <transition name="fade">
