                <label>City</label>
                <div class="innerFormGroup">
                  <select v-model="user.home.city" class="appInnerInput" :class="{ errorText: user.home.errors.city }">
                    <option v-bind:value="''">-- select --</option>
                    <option v-for="city in user.home.cities" v-bind:value="city.id">{{ city.name }}</option>
                  </select>
                </div>
              </div>
              <div class="form-group">
                <button class="appPrimaryBtn btnFulWidth marginTop15">Save</button>
              </div>
            </form>
          </div>
        </transition>
      </div>
      <!-- Residing address -->
