          <span class="sectionHeaderText">Residing Address</span>
          <i class="material-icons profileExpandIcon" v-if="!isResidingAddrExpanded" v-on:click="isResidingAddrExpanded = !isResidingAddrExpanded">add</i>
          <i class="material-icons profileCollapseIcon" v-if="isResidingAddrExpanded" v-on:click="isResidingAddrExpanded = !isResidingAddrExpanded">remove</i>
        </div>
        <transition name="fade">
          <div v-if="isResidingAddrExpanded" class="sectionContent">
            <!-- <p><input type="checkbox" name=""> Same as Home Address</p> -->
