                    <option v-bind:value="''">-- select --</option>
                    <option v-for="country in core.countries" v-bind:value="country.id">{{ country.name }}</option>
                  </select>
                </div>
              </div>
              <div class="form-group">
                <label>State</label>
                <div class="innerFormGroup">
                  <select v-model="user.residing.state" class="appInnerInput" :class="{ errorText: user.residing.errors.state }" v-on:change="stateChanged('residing')">
                    <option v-bind:value="''">-- select --</option>
                    <option v-for="state in user.residing.states" v-bind:value="state.id">{{ state.name }}</option>
                  </select>
                </div>
              </div>
              <div class="form-group">
                <label>City</label>
                <div class="innerFormGroup">
                  <select v-model="user.residing.city" class="appInnerInput" :class="{ errorText: user.residing.errors.city }">
