          <i class="material-icons profileExpandIcon" v-if="!isOccupationExpanded" v-on:click="isOccupationExpanded = !isOccupationExpanded">add</i>
          <i class="material-icons profileCollapseIcon" v-if="isOccupationExpanded" v-on:click="isOccupationExpanded = !isOccupationExpanded">remove</i>
        </div>
        <transition name="fade">
          <div v-if="isOccupationExpanded" class="sectionContent">
            <form class="form" v-on:submit.prevent="saveOccupation">
              <p class="appAlert" v-if="user.occupation.isOccupationFinished" v-bind:class="{ alertSuccess: user.occupation.isOccupationSuccess, alertError: user.occupation.isOccupationError }">{{ user.occupation.occupationAlertmsg }}</p>
              <div class="form-group">
                <label>Occupation</label>
                <div class="innerFormGroup">
                  <select v-model="user.occupation.occupation" v-on:change="occupationChange" class="appInnerInput" :class="{ errorText: user.occupation.errors.occupation }">
                    <option value="">-- select --</option>
                    <option v-for="occupation in core.occupations" v-bind:value="occupation.id">{{ occupation.title }}</option>
                    <option v-bind:value="'other'">Other</option>
                  </select>
                </div>
              </div>
              <div v-if="user.occupation.isOccupationNew" class="form-group">
                <label>Enter your Occupation</label>
                <div class="innerFormGroup">
                  <input type="text" v-model="user.occupation.newOccupation" class="appInnerInput" :class="{ errorText: user.occupation.errors.newOccupation }" placeholder="Enter your Occupation">
                </div>
              </div>
              <div v-if="user.occupation.occupation == 6" class="form-group">
                <label>Select What are you Studying</label>
                <div class="innerFormGroup">
                  <select class="appInnerInput" v-model="user.occupation.studentClass" :class="{ errorText: user.occupation.errors.studentClass }" v-on:change="studentClassChange">
                    <option v-bind:value="''">-- select --</option>
                    <option v-for="claz in core.classes" :value="claz.id">{{ claz.name }}</option>
                    <option v-bind:value="'other'">Other</option>
                  </select>
                </div>
              </div>
              <div v-if="user.occupation.isStudentClassNew" class="form-group">
                <label>Enter What are you Studying</label>
                <div class="innerFormGroup">
                  <input type="text" v-model="user.occupation.newStudentClass" class="appInnerInput" :class="{ errorText: user.occupation.errors.newStudentClass }" placeholder="Enter What you're Studying">
                </div>
              </div>
              <div class="form-group" v-if="user.occupation.occupation != 6">
                <label>Year started</label>
                <div class="innerFormGroup">
                  <select class="appInnerInput" v-model="user.occupation.from"  :class="{ errorText: user.occupation.errors.from }">
                    <option v-bind:value="''">-- select --</option>
                    <option v-for="year in years" :value="year">{{ year }}</option>
                  </select>
                </div>
              </div>
              <div class="form-group">
                <button class="appPrimaryBtn btnFulWidth marginTop15">Save</button>
              </div>
            </form>
          </div>
        </transition>
      </div>
