      <!-- <div class="section">
        <div class="sectionHeader">
          <span class="sectionHeaderText">Location</span>
          <i class="material-icons profileExpandIcon" v-if="!isLocationExpanded" v-on:click="isLocationExpanded = !isLocationExpanded">add</i>
          <i class="material-icons profileCollapseIcon" v-if="isLocationExpanded" v-on:click="isLocationExpanded = !isLocationExpanded">remove</i>
        </div>
        <transition name="fade">
          <div v-if="isLocationExpanded" class="sectionContent">
            <p class="appAlert" v-if="user.location.isLocationFinished" v-bind:class="{ alertSuccess: user.location.isLocationSuccess, alertError: user.location.isLocationError }">{{ user.location.locationAlertmsg }}</p>
            <p>Please make sure that you're in the location which you want to make it as your default location.</p>
            <div class="form-group">
              <div id="map-canvas"></div>
              <gmap-map :center="{lat: user.location.currentLocation.lat, lng: user.location.currentLocation.lng}" :zoom="12" style="width: 100%; height: 250px">
