                  ></gmap-marker>
                </gmap-map>
            </div>
            <span>Clicking on the below button will save the current location as your default location.</span>
            <div class="form-group">
              <button class="appSecondaryBtn btnFulWidth marginTop15" v-on:click="geolocation">Re-load Current Location</button>
              <button class="appPrimaryBtn btnFulWidth marginTop15" v-on:click="updateUserLocation">Save as Default Location</button>
            </div>
          </div>
        </transition>
      </div> -->
      <!-- Physical Attributes -->
      <div class="section">
        <div class="sectionHeader">
          <span class="sectionHeaderText">Physical Attributes</span>
          <i class="material-icons profileExpandIcon" v-if="!isPhysicalAttrsExpanded" v-on:click="isPhysicalAttrsExpanded = !isPhysicalAttrsExpanded">add</i>
          <i class="material-icons profileCollapseIcon" v-if="isPhysicalAttrsExpanded" v-on:click="isPhysicalAttrsExpanded = !isPhysicalAttrsExpanded">remove</i>
        </div>
        <transition name="fade">
          <div v-if="isPhysicalAttrsExpanded" class="sectionContent">
            <form class="form" v-on:submit="savePhysicalAttrs">
              <p class="appAlert" v-if="user.physical.isPhysicalAttrFinished" v-bind:class="{ alertSuccess: user.physical.isPhysicalAttrSuccess, alertError: user.physical.isPhysicalAttrError }">{{ user.physical.physicalAttrAlertmsg }}</p>
              <div class="form-group">
                <label>Blood Group</label>
                <div class="innerFormGroup">
                  <select v-model="user.physical.bloodGroup" class="appInnerInput" :class="{ errorText: user.physical.errors.bloodGroup }">
                    <option value="">-- select --</option>
                    <option v-for="bloodGroup in core.bloodGroups" v-bind:value="bloodGroup.id">{{ bloodGroup.name }}</option>
                  </select>
                </div>
              </div>
              <div class="form-group">
                <label>Height (cm)</label>
                <div class="innerFormGroup">
                  <input type="text" v-model="user.physical.height" class="appInnerInput" placeholder="Height (cm)" :class="{ errorText: user.physical.errors.height }">
