import Datepicker from 'vuejs-datepicker'
import { mapGetters } from 'vuex'

export default {
  name: 'EditProfile',
  data () {
    return {
      apiUrl: Vue.config.apiUrl,
    	storagePath: Vue.config.storagePath,
      title: 'Edit Profile',
      user: {
        primary: {
          name: '',
          avatar: '',
          gender: '',
          dob: '',
          mobile: '',
          avatarSrc: require('../assets/images/avatar.png'),
          isPrimaryFinished: false,
          isPrimarySuccess: false,
          isPrimaryError: false,
          primaryAlertmsg: '',
