            name: false,
            avatar: false,
            gender: false,
            dob: false,
            mobile: false
          },
          error: false
        },
        password: {
          currPassword: '',
          confirmPassword: '',
          newPassword: '',
          isPasswordFinished: false,
          isPasswordSuccess: false,
          isPasswordError: false,
          passwordAlertmsg: '',
          errors: {
            currPassword: false,
            confirmPassword: false,
            newPassword: false
          },
          error: false
        },
        home: {
          address1: '',
          address2: '',
          address3: '',
          city: '',
          state: '',
          country: '',
          isHomeFinished: false,
          isHomeSuccess: false,
          isHomeError: false,
          homeAlertmsg: '',
          errors: {
            address1: false,
            city: false,
            state: false,
            country: false
