          error: false,
          states: [],
          cities: []
        },
        residing: {
          address1: '',
          address2: '',
          address3: '',
          city: '',
          state: '',
          country: '',
          isResidingFinished: false,
          isResidingSuccess: false,
          isResidingError: false,
          residingAlertmsg: '',
