  methods: {
    occupationChange: function() {

      if(this.user.occupation.occupation == 'other')
        this.user.occupation.isOccupationNew = true;
      else
        this.user.occupation.isOccupationNew = false;

      if(this.user.occupation.occupation == 6) // Students
        this.user.occupation.isStudent = true;
      else
        this.user.occupation.isStudent = false;

    },
    studentClassChange: function() {

      if(this.user.occupation.studentClass == 'other')
        this.user.occupation.isStudentClassNew = true;
      else
        this.user.occupation.isStudentClassNew = false;

    },
    avatarChoosen: function(event) {

      this.user.primary.avatar = event.target.files[0];

      var input = event.target;
      if(input.files && input.files[0]) {
          // create a new FileReader to read this image and convert to base64 format
          var reader = new FileReader();
          // Define a callback function to run, when FileReader finishes its job
          reader.onload = (e) => {
              // Read image as base64 and set to imageData
              this.user.primary.avatarSrc = e.target.result;
          }
          // Start the reader job - read file as a data url (base64 format)
          reader.readAsDataURL(input.files[0]);
      }

    },
    countryChanged: function(from) {

      this.$store.commit('setIsPageLoading', true);
      var postData = { 'countryId': this.user[from].country };
      this.$http.post(this.apiUrl + 'getStatesByCountry', postData, {
        headers: {
          'userToken': this.userAccessToken,
        }
      }).then(response => {
            this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            if(body.status == 'success') {
              /*this.user.home.isHomeFinished = this.user.home.isHomeSuccess = true;
              this.user.home.homeAlertmsg = '';*/
              this.user[from].states = body.states;
            } else if(body.status == 'error') {
              /*this.user.home.homeAlertmsg = body.message;
              this.user.home.isHomeSuccess = false;
              this.user.home.isHomeFinished = this.user.home.isHomeError = true;*/
            }
        }).catch(function(err) {
          this.$store.commit('setIsPageLoading', false);
          /*this.user.home.homeAlertmsg = 'Something went wrong, please try again';
          this.user.home.isHomeSuccess = false;
          this.user.home.isHomeFinished = this.user.home.isHomeError = true;*/
          console.log('error', err);
        })

    },
