    savePrimaryDetails: function() {

      var _primary = this.user.primary;
      _primary.error = false;
      /*if(!_primary.avatar)
        _primary.error = _primary.errors.avatar = true;
      else
        _primary.errors.avatar = false;*/

      if(!_primary.name)
        _primary.error = _primary.errors.name = true;
      else
        _primary.errors.name = false;

      if(!_primary.gender)
        _primary.error = _primary.errors.gender = true;
      else
        _primary.errors.gender = false;

      if(!_primary.dob)
        _primary.error = _primary.errors.dob = true;
      else
        _primary.errors.dob = false;

      if(!_primary.mobile)
        _primary.error = _primary.errors.mobile = true;
      else
        _primary.errors.mobile = false;

      if(_primary.error) return false;

      var postData = new FormData();
      postData.append('avatar', _primary.avatar);
      postData.append('fullName', _primary.name);
      postData.append('gender', _primary.gender);
      postData.append('dob', this.convertDate(_primary.dob));
      postData.append('mobileNo', _primary.mobile);
      this.$store.commit('setIsPageLoading', true);
      this.$http.post(this.apiUrl + 'updatePrimaryDetails', postData, {
        headers: {
          'userToken': this.userAccessToken,
          'Content-Type': 'multipart/form-data'
        }
      }).then(response => {
            this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            if(body.status == 'success') {
              _primary.primaryAlertmsg = 'Primary details have been saved';
              _primary.isPrimaryError = false;
              _primary.isPrimaryFinished = _primary.isPrimarySuccess = true;
            } else if(body.status == 'error') {
              _primary.primaryAlertmsg = body.message;
              _primary.isPrimarySuccess = false;
              _primary.isPrimaryFinished = _primary.isPrimaryError = true;
            }
        }).catch(function(err) {
          this.$store.commit('setIsPageLoading', false);
          _primary.primaryAlertmsg = 'Something went wrong, please try again';
          _primary.isPrimarySuccess = false;
          _primary.isPrimaryFinished = _primary.isPrimaryError = true;
          console.log('error', err);
        })
    },
    updatePassword: function() {

      var _password = this.user.password;
      _password.error = false;
      if(!_password.currPassword)
        _password.error = _password.errors.currPassword = true;
      else
        _password.errors.currPassword = false;

      if(!_password.newPassword)
        _password.error = _password.errors.newPassword = true;
      else
        _password.errors.newPassword = false;

      if(!_password.confirmPassword)
        _password.error = _password.errors.confirmPassword = true;
      else if(_password.newPassword != _password.confirmPassword)
        _password.error = _password.errors.confirmPassword = true;
      else
        _password.errors.confirmPassword = false;

      if(_password.error) return false;

      var postData = new FormData();
      postData.append('currPassword', _password.currPassword);
      postData.append('newPassword', _password.newPassword);
      this.$store.commit('setIsPageLoading', true);
      this.$http.post(this.apiUrl + 'updatePassword', postData, {
        headers: {
          'userToken': this.userAccessToken,
        }
      }).then(response => {
            this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            _password.currPassword = _password.newPassword = _password.confirmPassword = '';
            if(body.status == 'success') {
              _password.passwordAlertmsg = 'Your password has been saved';
              _password.isPasswordError = false;
              _password.isPasswordFinished = _password.isPasswordSuccess = true;
            } else if(body.status == 'error') {
              _password.passwordAlertmsg = body.message;
              _password.isPasswordSuccess = false;
              _password.isPasswordFinished = _password.isPasswordError = true;
            }
        }).catch(function(err) {
          this.$store.commit('setIsPageLoading', false);
          _password.currPassword = _password.newPassword = _password.confirmPassword = '';
          _password.passwordAlertmsg = 'Something went wrong, please try again';
          _password.isPasswordSuccess = false;
          _password.isPasswordFinished = _password.isPasswordError = true;
          console.log('error', err);
        })
    },
