    saveOccupation: function() {

      var _occupation = this.user.occupation;
      _occupation.error = false;
      if(!_occupation.occupation)
        _occupation.error = _occupation.errors.occupation = true;
      else
        _occupation.errors.occupation = false;

      if(_occupation.occupation == 'other') {
        if(!_occupation.newOccupation)
          _occupation.error = _occupation.errors.newOccupation = true;
        else
          _occupation.errors.newOccupation = false;
      }

      if(_occupation.occupation == 6) {
        if(!_occupation.studentClass)
          _occupation.error = _occupation.errors.studentClass = true;
        else
          _occupation.errors.studentClass = false;
        
        if(_occupation.studentClass == 'other') {
          if(!_occupation.newStudentClass)
            _occupation.error = _occupation.errors.newStudentClass = true;
          else
            _occupation.errors.newStudentClass = false;
        }
      } else {
        if(!_occupation.from)
          _occupation.error = _occupation.errors.from = true;
        else
          _occupation.errors.from = false;
      }

      // console.log('_occupation.error', _occupation.error); return false;
      if(_occupation.error) return false;

      var postData = new FormData();
      postData.append('occupation', _occupation.occupation);
      if(_occupation.occupation == 'other')
        postData.append('newOccupation', _occupation.newOccupation);
      if(_occupation.occupation == 6)
        postData.append('studentClass', _occupation.studentClass);
      else
        postData.append('from', _occupation.from);
      if(_occupation.studentClass == 'other')
        postData.append('newClass', _occupation.newStudentClass);
      
      this.$store.commit('setIsPageLoading', true);
      this.$http.post(this.apiUrl + 'updateOccupation', postData, {
        headers: {
          'userToken': this.userAccessToken,
        }
      }).then(response => {
            this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            if(body.status == 'success') {
              _occupation.occupationAlertmsg = 'Occupation has been saved';
              _occupation.isOccupationError = false;
              _occupation.isOccupationFinished = _occupation.isOccupationSuccess = true;
            } else if(body.status == 'error') {
              _occupation.occupationAlertmsg = body.message;
              _occupation.isOccupationSuccess = false;
              _occupation.isOccupationFinished = _occupation.isOccupationError = true;
            }
        }).catch(function(err) {
          this.$store.commit('setIsPageLoading', false);
          _occupation.occupationAlertmsg = 'Something went wrong, please try again';
          _occupation.isOccupationSuccess = false;
          _occupation.isOccupationFinished = _occupation.isOccupationError = true;
          console.log('error', err);
        })
    },
