    getProfileDetails: function() {

      var _load = this.user.load;
      this.$store.commit('setIsPageLoading', true);
      this.$http.post(this.apiUrl + 'getProfileDetails', {}, {
        headers: {
          'userToken': this.userAccessToken,
        }
      }).then(response => {
            this.$store.commit('setIsPageLoading', false);
            var body = response.body;
            if(body.status == 'success') {
              // console.log('body', body);
              this.bindUserDetails(body.data, this.storagePath);

            } else if(body.status == 'error') {
              _load.error = true;
              _load.errorText = 'We couldn\'t load your data, please try again';
            }
        }).catch(function(err) {
          this.$store.commit('setIsPageLoading', false);
          _load.error = true;
          _load.errorText = 'Something went wrong, please try again';
          console.log('error', err);
        })

    },
    bindUserDetails: function(userData, storagePath) {

      var userSections = ['primary', 'password', 'home', 'residing', 'occupation', 'location', 'physical', 'emergency', 'services'];
      var _user = this.user;
      var self = this;
      userSections.forEach(function(key, val){
        var sectionData = userData[key];
        switch(key) {

          case 'primary':

            if(sectionData) {
              var _primary = _user.primary;
              _primary.avatarSrc = storagePath + sectionData.avatar + '?' + Math.random();
              _primary.name = sectionData.name;
              _primary.gender = sectionData.gender;
              _primary.dob = sectionData.dob;
              _primary.mobile = sectionData.mobile;
            }
            break;

          case 'home':
              
            if(sectionData) {
              var _home = _user.home;
              _home.address1 = sectionData.address1;
              _home.address2 = sectionData.address2;
              _home.address3 = sectionData.address3;
              _home.country = sectionData.country;
              _home.states = sectionData.homeStates;
              _home.cities = sectionData.homeCities;
              _home.state = sectionData.state;
              _home.city = sectionData.city;
            }
            break;

          case 'residing':

            if(sectionData) {
              var _residing = _user.residing;
              _residing.address1 = sectionData.address1;
              _residing.address2 = sectionData.address2;
              _residing.address3 = sectionData.address3;
              _residing.country = sectionData.country;
              _residing.states = sectionData.residingStates;
              _residing.cities = sectionData.residingCities;
              _residing.state = sectionData.state;
              _residing.city = sectionData.city;
            }
            break;

          case 'occupation':

            if(sectionData) {
              var _occupation = _user.occupation;
              _occupation.occupation = sectionData.occupation;
              _occupation.studentClass = sectionData.studentClass;
              _occupation.from = sectionData.from;
            }
            break;

          case 'location':
            
            if(sectionData) {
              var _location = _user.location;
              _location.currentLocation.lat = parseFloat(sectionData.lat);
              _location.currentLocation.lng = parseFloat(sectionData.lng);
            }
            break;

          case 'physical':

            if(sectionData) {
              var _physical = _user.physical;
              _physical.bloodGroup = sectionData.bloodGroup;
              _physical.height = sectionData.height;
              _physical.weight = sectionData.weight;
            }
            break;

          case 'emergency':

            if(sectionData) {
              var _emergency = _user.emergency;
              _emergency.contact = sectionData.contact;
              _emergency.relation = sectionData.relation;
              _emergency.number = sectionData.number;
            }
            break;

          case 'services':
            if(sectionData) {
              self.core.services = sectionData;
            }
            break;

        }

      })

    }
  },
  computed: {

    ...mapGetters(['userAccessToken']),
    
    years: function() {
      var yrs = [];
      var date = new Date();
      var currYear = date.getFullYear();
      console.log();
      var startYear = currYear - 80;
      for(var i = startYear; i <= currYear; i++) {
        yrs.push(i);
      }
      return yrs;
    }

  },
  created: function() {

    this.getProfileCoreData();
    this.getProfileDetails();

  },
  mounted: function() {
    // this.geolocation();
  },
  components: {
    Datepicker
  }
}
</script>

