<template>
  <div id="login">
    <div class="wrapper">
      <h3 class="pageTitle">{{ title }}</h3><br>
		<!-- <p class="loginWithText textCenter">--&nbsp; Signup with &nbsp;--</p>
		<facebook-login class="button btnFulWidth FBLoginBtn" :appId="facebook.appID" :loginLabel="'Facebook Account'" v-on:login="facebookLogin" :loginOptions="facebook.loginOptions"></facebook-login>
    <div class="googleLogin">
      <span class="googleLoginIcon"></span>
      <g-signin-button :params="googleSignInParams" v-on:success="googleLoginSuccess" v-on:error="googleLoginError">Google Account</g-signin-button>
    </div>
		<p class="textCenter">or</p> -->
		<form class="appForm" v-on:submit.prevent="registerUser">
			<p class="textCenter formError errorText" v-if="signup.errorText">{{ signup.errorText }}</p>
			<div class="formGroup">
				<input type="text" v-model="signup.fullName" class="appInput signupInput" :class="{ errorText: signup.isFullNameError }" placeholder="Full Name">
			</div>
			<div class="formGroup">
				<input type="text" v-model="signup.email" class="appInput signupInput" :class="{ errorText: signup.isEmailError }" placeholder="Email">
			</div>
			<div class="formGroup">
				<input type="password" v-model="signup.password" class="appInput signupInput" :class="{ errorText: signup.isPasswordError }" placeholder="Password">
			</div>
			<div class="formGroup">
				<input type="password" v-model="signup.confirmPassword" class="appInput signupInput" :class="{ errorText: signup.isConfirmPasswordError }" placeholder="Confirm Password">
			</div>
			<div class="formGroup">
				<button type="submit" class="appPrimaryBtn btnFulWidth">Create Account</button>
			</div>
		</form>
		<ul class="appBottomLinks">
			<li><a href="#/">Back to Login</a></li>
			<li><a href="#forgot">Forgot password?</a></li>
		</ul>
	</div>
  </div>
</template>

<script>

import Vue from 'vue'
// import facebookLogin from 'facebook-login-vuejs'
// import GSignInButton from 'vue-google-signin-button'

// Vue.use(GSignInButton)

export default {
  name: 'Register',
  data () {
    return {
      title: 'Register',
    	signup: {
    		fullName: '',
    		email: '',
    		password: '',
    		confirmPassword: '',
    		isFullNameError: '',
    		isEmailError: '',
    		isPasswordError: '',
    		isConfirmPasswordError: '',
    		errorText: ''
    	},
    	isError: false,
    	apiUrl: Vue.config.apiUrl,
      facebook: {
        loginOptions: {
          scope: 'email'
        },
        appID: Vue.config.facebookAppID,
      },
      googleSignInParams: {
        client_id: Vue.config.googleClientID + '.apps.googleusercontent.com',
        scope: 'email profile',
        cookiepolicy: 'single_host_origin',
      }
    }
  },
  methods: {
  	registerUser: function() {

  		this.isError = false;
      	this.signup.errorText = '';
  		if(!this.signup.fullName) {
  			this.isError = this.signup.isFullNameError = true;
  		} else {
  			this.signup.isFullNameError = false;
  		}
  		if(!this.signup.email || !this.validateEmail(this.signup.email)) {
  			this.isError = this.signup.isEmailError = true;
  		} else {
  			this.signup.isEmailError = false;
  		}
  		if(!this.signup.password) {
  			this.isError = this.signup.isPasswordError = true;
  		} else {
  			this.signup.isPasswordError = false;
  		}
  		if(!this.signup.confirmPassword) {
  			this.isError = this.signup.isConfirmPasswordError = true;
  		} else if(this.signup.password !== this.signup.confirmPassword) {
  			this.isError = this.signup.isConfirmPasswordError = true;
  		} else {
  			this.signup.isConfirmPasswordError = false;
  		}

  		if(this.isError) return false;

  		this.$http.post(this.apiUrl + 'signup',
            {
                fullName: this.signup.fullName,
                username: this.signup.email,
                password: this.signup.password
            }
        ).then(response => {
            var body = response.body;
            if(body.status == 'success') {
                this.$store.commit('setUserAccessToken', body.token);
                this.$store.commit('setUserId', body.userId);
                this.$store.commit('setIsUserActive', true);
                window.location.href = '#/dashboard';

            } else if(body.status == 'error') {
                this.signup.isError = true;
                this.signup.errorText = body.message;
                this.signup.password = this.signup.confirmPassword = '';
            }
        })
  	},
    /*facebookLogin: function(response) {
      var response = response.response;
      if(response.status == 'connected') {
        FB.api('/me', { fields: 'email,name' }, user => {
          var userData = {
              fullName: user.name,
              username: user.email,
              accessToken: response.authResponse.accessToken,
              source: 'facebook'
          };
          this.socialLogin(userData);
        })
      }
    },
    googleLoginSuccess: function(googleUser) {
      var profile = googleUser.getBasicProfile() // etc etc
      var userData = {
        fullName: profile.getName(),
        username: profile.getEmail(),
        accessToken: googleUser.Zi.access_token,
        source: 'google'
      };
      this.socialLogin(userData);
    },
    googleLoginError (error) {
      console.log('error', error)
    },
    socialLogin: function(data) {
      this.$store.commit('setIsPageLoading', true);
      this.$http.post(this.apiUrl + 'socialLogin', data).then(response => {
        this.$store.commit('setIsPageLoading', false);
        var body = response.body;
        if(body.status == 'success') {
            this.$store.commit('setUserAccessToken', body.token);
            this.$store.commit('setUserId', body.userId);
            this.$store.commit('setIsUserActive', true);
            window.location.href = body.userHasPassword ? '#/dashboard' : '#/choosePassword';

        } else if(body.status == 'error') {
            this.login.isError = true;
            this.login.errorText = body.message;
            this.login.password = '';
        }
      }).catch(function(error){
        this.$store.commit('setIsPageLoading', false);
        console.log('error', error);
      })
    },*/
  	validateEmail: function(email) {
  		var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
  	}
  },
  components: {
    // facebookLogin
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>

.wrapper {
	margin-top: 5%;
}

</style>



