<template>
  <div id="header">
    <div id="appHeader">
      <i class="material-icons userMenuIcon" v-if="isUserActive" v-on:click="menuIconClick">menu</i>
      <i class="material-icons headerHomeIcon" v-if="isUserActive" v-on:click="redirectToHome">home</i>
      <span>MyHomeTown App</span>
      <i class="material-icons headMoreIcon" v-if="isUserActive" v-on:click="moreMenuClick">more_vert</i>
    </div>
    <app-menu v-if="isUserActive && showMenu"></app-menu>
    <more-menu v-if="isUserActive && showMore"></more-menu>
  </div>
</template>

<script>

import { mapGetters } from 'vuex'

import '../assets/css/style.css'
import '../assets/css/googleFont.css'
import '../assets/css/meterialIcons.css'
import '../assets/css/bootstrap.min.css'
// import '../assets/js/app.js'

export default {
  name: 'Header',
  data () {
    return {
      showMenu: false,
      showMore: false,
    }
  },
  methods: {
    redirectToHome: function() {
      this.$root.$emit('moreMenuEvent', false);
      this.showMenu = false;
      window.location.href = '#/dashboard';
    },
    menuIconClick: function() {
      this.showMenu = !this.showMenu;
      this.$root.$emit('moreMenuEvent', false);
    },
    moreMenuClick: function() {
      this.showMore = !this.showMore;
      this.showMenu = false;
    }
  },
  computed: {
    ...mapGetters(['isUserActive'])
  },
  mounted: function() {
    this.$root.$on('moreMenuEvent', (value) => {
      this.showMore = value;
    });
    this.$root.$on('menuClickEvent', (value) => {
      this.showMenu = value;
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

#appHeader {
  background: #ca7621;
  padding: 15px 0;
  text-align: center;
  color: #fff;
}

</style>



